import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { VerticalPlacement } from '../../common';
import { DividerBase } from '../_helpers/divider.base';

/**
 * Vertical Divider
 *
 * @export
 * @class VerticalDividerComponent
 * @typedef {VerticalDividerComponent}
 * @extends {DividerBase}
 */
@Component({
  selector: 'eds-vertical-divider',
  templateUrl: './vertical.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class VerticalDividerComponent extends DividerBase {
  /**
   * Text placement within divider
   *
   * @type {VerticalPlacement}
   */
  @Input() textAlign: VerticalPlacement = 'center';

  /**
   * @readonly
   * @type {string}
   */
  get verticalClass(): string {
    return this.twClasses(this.textAlign);
  }
}
