import { Directive, Input } from '@angular/core';
import { BannerType, BaseInputs } from '../../common';

/**
 * Base class for Banners
 *
 * @export
 * @class BannerBase
 * @typedef {BannerBase}
 * @extends {BaseInputs}
 */
@Directive()
export class BannerBase extends BaseInputs {
  /**
   * Link URL / Route
   *
   * @type {string}
   */
  @Input() link: string = '';

  /**
   * Link text to be displayed
   *
   * @type {string}
   */
  @Input() linkText: string = '';

  /**
   * Determines what banner is shown
   *
   * @type {BannerType}
   */
  @Input() set status(status: BannerType) {
    {
      switch (status) {
        case 'error':
          this._icon = 'components/assets/icons/error-solid.svg';
          this._color = 'red';
          this._borderColor = 'border-red';
          break;
        case 'success':
          this._icon = 'components/assets/icons/success-solid.svg';
          this._color = 'green-dark';
          this._borderColor = 'border-green-dark';
          break;
        case 'warn':
          this._icon = 'components/assets/icons/warning-solid.svg';
          this._color = 'yellow-dark';
          this._borderColor = 'border-yellow-dark';
          break;
        case 'info':
        default:
          this._icon = 'components/assets/icons/information-solid.svg';
          this._color = 'gray-darker';
          this._borderColor = 'border-gray-darker';
      }
    }
  }

  /**
   * @private
   * @type {string}
   */
  private _icon: string = '';
  /**
   * @private
   * @type {string}
   */
  private _color: string = '';
  /**
   * @private
   * @type {string}
   */
  private _borderColor: string = '';
  /**
   * @readonly
   * @type {string}
   */
  get icon(): string {
    return this._icon;
  }
  /**
   * @readonly
   * @type {string}
   */
  get color(): string {
    return this._color;
  }
  /**
   * @readonly
   * @type {string}
   */
  get borderColor(): string {
    return this._borderColor;
  }
}
