import { Directive, Input } from '@angular/core';
import { FormBaseInputs } from '../../common';
import { InputSize, InputState, InputStatus } from '.';

/**
 * Form Inputs that most components will extend
 *
 * @export
 * @class BaseInputs
 * @typedef {FormBaseInputs}
 */
@Directive()
export class InputBaseInputs extends FormBaseInputs {
  /**
   * Form Label Size
   *
   * @type {FormControl}
   */
  @Input() size: InputSize = 'md';
  /**
   * Input has six possible states â€” enabled, focus, active, filled, disabled, and read-only.
   *
   * @type {InputState}
   */
  @Input() state?: InputState = undefined;
  /**
   * Input can display a status which can be used for form validation. The four status types are success, loading, warning, and error.
   *
   * @type {InputStatus}
   */
  @Input() status?: InputStatus = undefined;
}
