import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { BaseInputs } from '../common';
import { ContentModel } from './_helpers';

type AccordionItem = {
  id: number;
  isExpanded: boolean;
  content: ContentModel;
};

/**
 * Accordion component
 *
 * @export
 * @class AccordionComponent
 * @typedef {AccordionComponent}
 * @extends {BaseInputs}
 */
@Component({
  selector: 'eds-accordion',
  templateUrl: './accordion.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccordionComponent extends BaseInputs {
  /**
   * Array of ContentModel
   *
   * @type {ContentModel[]}
   */
  @Input() set items(items: Array<ContentModel>) {
    if (items) {
      this._items = [
        ...items.map((item, i) => ({
          id: i,
          isExpanded: false,
          content: { ...item },
        })),
      ];
    }
  }

  /**
   * @private
   * @type {AccordionItem[]}
   */
  private _items: AccordionItem[] = new Array<AccordionItem>();

  /**
   * Returns items from input
   *
   * @public
   * @returns {AccordionItem[]}
   */
  public getItems(): AccordionItem[] {
    return this._items ?? new Array<AccordionItem>();
  }

  /**
   * Checks if accordion item is expanded
   *
   * @public
   * @param {AccordionItem} item
   * @returns {boolean}
   */
  public expanded(item: AccordionItem): boolean {
    return item.isExpanded ?? false;
  }

  /**
   * Toggles expansion of accordion item
   *
   * @public
   * @param {AccordionItem} item
   */
  public toggleExpansion(item: AccordionItem): void {
    item.isExpanded = !item.isExpanded;
  }
}
