import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { BannerBase } from '../_helpers';

/**
 * Banner to display with list of items (cannot be dismissed by user)
 *
 * @export
 * @class InlineListBannerComponent
 * @typedef {InlineListBannerComponent}
 * @extends {BannerBase}
 */
@Component({
  selector: 'eds-inline-list-banner',
  templateUrl: './inline-list-banner.component.html',
  styleUrls: ['./inline-list-banner.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class InlineListBannerComponent extends BannerBase {
  /**
   * Main title of the banner
   *
   * @type {string}
   */
  @Input() title: string = '';

  /**
   * List of messages/texts to display
   *
   * @type {string[]}
   */
  @Input() messages: string[] = new Array<string>();
}
