import { Component, Input } from '@angular/core';
import { DEFAULT_IMG_ALT, DEFAULT_IMG_URL } from './_helpers/defaults';
import { ImageMeta } from '../common';
@Component({
  selector: 'eds-hero',
  templateUrl: './hero.component.html',
})
export class HeroComponent {
  /**
   * Displayed Title
   *
   * @type {string}
   */
  @Input() title?: string;
  /**
   * Displayed description
   *
   * @type {string}
   */
  @Input() description?: string;
  /**
   * Url for background image
   *
   * @type {string}
   */
  /**
   * Image data
   *
   * @type {ImageMeta}
   */
  @Input() imageData: ImageMeta = {
    src: DEFAULT_IMG_URL,
    altText: DEFAULT_IMG_ALT,
  };
  /**
   * Label for primary button
   *
   * @type {string}
   */
  @Input() primaryBtnLabel?: string;
  /**
   * Href for primary button
   *
   * @type {string}
   */
  @Input() primaryBtnLink?: string;
  /**
   * Label for secondary button
   *
   * @type {string}
   */
  @Input() secondaryBtnLabel?: string;
  /**
   * Href for secondary button
   *
   * @type {string}
   */
  @Input() secondaryBtnLink?: string;
}
