/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  Directive,
  EventEmitter,
  Input,
  Output,
  TemplateRef,
} from '@angular/core';
import { BaseInputs, ModalPlacement } from '../../common';

/**
 * Modals Base
 *
 * @export
 * @class ModalsBase
 * @typedef {ModalsBase}
 * @extends {BaseInputs}
 */
@Directive()
export class ModalsBase extends BaseInputs {
  /**
   * Determines if modal is open or not (can manually opened/closed if needed)
   *
   * @type {boolean}
   */
  @Input() isOpen: boolean = false;

  /**
   * Determines if modal can be dismissed without interaction
   *
   * @type {boolean}
   */
  @Input() allowClose: boolean = false;

  /**
   * Modals title in <h2>
   *
   * @type {string}
   */
  @Input() title: string = 'Modal Title..';

  /**
   * Content/description text to be displayed to user
   * @optional
   * @type {string}
   */
  @Input() content?: string;

  /**
   * Content in the form of a template defined by user (optional)
   * @optional
   * @type {?TemplateRef<any>}
   */
  @Input() contentTemplate?: TemplateRef<any>;

  /**
   * Text for primary button
   *
   * @type {string}
   */
  @Input() primaryBtnText: string = 'Yes';

  /**
   * Tailwind classes to further style the primary button
   *
   * @type {string}
   */
  @Input() primaryBtnTwClasses: string = 'btn-sm btn-full';

  /**
   * Text for secondary button
   *
   * @type {string}
   */
  @Input() secondaryBtnText: string = 'No';

  /**
   * Tailwind classes to further style the secondary button
   *
   * @type {string}
   */
  @Input() secondaryBtnTwClasses: string = 'btn-sm btn-full';

  /**
   * Where modal will open on the screen
   *
   * @type {ModalPlacement}
   */
  @Input() alignment: ModalPlacement = 'center';

  /**
   * Event for primary button clicked
   *
   * @type {*}
   */
  @Output() primaryBtnClicked: any = new EventEmitter();

  /**
   * Event for secondary button clicked
   *
   * @type {*}
   */
  @Output() secondaryBtnClicked: any = new EventEmitter();

  /**
   * On Modal close
   *
   * @type {*}
   */
  @Output() modalClosed: any = new EventEmitter();

  /**
   * Gets classes for modal when open/closed
   *
   * @readonly
   * @type {string}
   */
  get modalOpenCloseClasses(): string {
    return this.isOpen
      ? 'opacity-100 transform scale-100 translate-y-0 ease-out duration-200'
      : 'opacity-0 transform scale-95 translate-y-1/3 sm:translate-y-0 ease-in duration-75';
  }

  /**
   * Gets classes for overlay background
   *
   * @readonly
   * @type {string}
   */
  get overlayBackgroundOpenCloseClasses(): string {
    return this.isOpen
      ? 'bg-opacity-80 ease-out duration-100'
      : 'bg-opacity-0 ease-in duration-150';
  }

  /**
   * Gets classes for overlay
   *
   * @readonly
   * @type {string}
   */
  get overlayOpenCloseClasses(): string {
    return this.isOpen ? 'visible' : 'invisible';
  }

  /**
   * @public
   */
  public handleSecondaryBtnClicked(): void {
    this.secondaryBtnClicked.emit();
    this.toggleModal();
  }

  /**
   * @public
   */
  public handlePrimaryBtnClicked(): void {
    this.primaryBtnClicked.emit();
    this.toggleModal();
  }

  /**
   * @public
   */
  public handleCloseBtnClicked(): void {
    this.toggleModal();
  }

  /**
   * @private
   */
  private toggleModal(): void {
    this.isOpen = !this.isOpen;
    if (!this.isOpen) {
      this.modalClosed.emit();
    }
  }
}
