import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { BaseInputs } from '../common';

/**
 * Big Icon Card
 *
 * @export
 * @class BigIconCardComponent
 * @typedef {BigIconCardComponent}
 * @extends {BaseInputs}
 */
@Component({
  selector: 'eds-big-icon-card',
  templateUrl: './big-icon-card.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BigIconCardComponent extends BaseInputs {
  /**
   * Main title of the card (header)
   *
   * @type {string}
   */
  @Input() cardTitle: string = '';

  /**
   * Subtext for card
   *
   * @type {string}
   */
  @Input() description: string = '';

  /**
   * Path to icon
   *
   * @type {string}
   */
  @Input() iconSrc: string = '';

  /**
   * URL / Route
   *
   * @type {string}
   */
  @Input() href: string = '';
}
