import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { HorizontalPlacement } from '../../common';
import { DividerBase } from '../_helpers/divider.base';

/**
 * Horizontal Divider
 *
 * @export
 * @class HorizontalDividerComponent
 * @typedef {HorizontalDividerComponent}
 * @extends {DividerBase}
 */
@Component({
  selector: 'eds-horizontal-divider',
  templateUrl: './horizontal.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HorizontalDividerComponent extends DividerBase {
  /**
   * Text placement within divider
   *
   * @type {HorizontalPlacement}
   */
  @Input() textAlign: HorizontalPlacement = 'center';

  /**
   * @readonly
   * @type {string}
   */
  get horizontalClass(): string {
    return this.twClasses(this.textAlign);
  }
}
