import { Component } from '@angular/core';
import { ValidatorEntry } from '../../common';
import { InputBaseInputs } from '../_helpers';
/**
 * Phone variant for input.
 *
 * @export
 * @class PhoneInputComponent
 * @typedef {PhoneInputComponent}
 * @extends {InputBaseInputs}
 */
@Component({
  selector: 'eds-phone-input',
  templateUrl: './phone-input.component.html',
})
export class PhoneInputComponent extends InputBaseInputs {
  /**
   * Phone mask for the input.
   *
   * @public
   * @readonly
   * @type {"(000) 000-0000"}
   */
  public readonly phoneMask = '(000) 000-0000';
  /**
   * Validators to ensure a proper phone number is entered.
   *
   * @public
   * @readonly
   * @type {ValidatorEntry[]}
   */
  public readonly phoneValidators: ValidatorEntry[] = [
    {
      key: 'mask',
      message: 'Valid phone number is required.',
      validator: undefined,
      validatorArgs: [],
      async: false,
    },
  ];
}
