import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { IconMeta, Placement, SvgMeta } from '../../common';
import { ButtonBase } from '../_helpers';

/**
 * Icon Button
 *
 * @export
 * @class IconButtonComponent
 * @typedef {IconButtonComponent}
 * @extends {ButtonBase}
 */
@Component({
  selector: 'eds-icon-button',
  templateUrl: './icon-button.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IconButtonComponent extends ButtonBase {
  /**
   * Where on the button the icon is displayed
   *
   * @type {Placement}
   */
  @Input() iconPlacement: Placement = 'left';

  /**
   * Icon data
   *
   * @type {IconMeta}
   */
  @Input() imageData: IconMeta = {
    src: '',
    iconClass: 'icon-24',
    altText: 'image',
  };

  /**
   * Svg Data
   *
   * @type {SvgMeta}
   */
  @Input() svgData: SvgMeta = {
    d: '',
    viewBox: '0 0 64 64',
    iconClass: 'icon-16',
  };

  /**
   * @readonly
   * @type {string}
   */
  get iconButtonClass(): string {
    return this.TwClassHelpers.buildIconButtonClass(
      this.iconPlacement,
      this.twClassesBasedOnInputs
    );
  }

  /**
   * @readonly
   * @type {string}
   */
  get iconClass(): string {
    return this.TwClassHelpers.buildIconClass(
      this.iconPlacement,
      this.imageData.iconClass
    );
  }
}
