import { Directive, EventEmitter, Input, Output } from '@angular/core';

import { BaseInputs, Placement } from '../../common';

@Directive()
export class CheckboxesBase extends BaseInputs {
  @Input() labelPlacement: Placement = 'left';
  @Input() isChecked = true;
  @Input() text = '';

  @Output() isCheckedChanged = new EventEmitter<Event>();

  public handleCheckedChanged($event: Event): void {
    this.isCheckedChanged.emit($event);
  }
}
