import { AfterViewInit, Component, Input, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { SvgComponent } from '../svg/svg.component';
import { Tab } from './_helpers';
import { DynamicComponentDirective } from './_helpers/dynamic-component.directive';
import { SvgAttributes } from '../svg';

@Component({
  selector: 'eds-tabs',
  templateUrl: './tabs.component.html',
})
export class TabsComponent implements AfterViewInit  {
  /**
   * An array of tabs to display.
   *
   * @type {Tab[]}
   */
    @Input() tabs: Tab[] = [];
  /**
   * Description of purpose for tabs.
   *
   * @type {string}
   */
   @Input() tabsAriaLabel: string = 'Tabs that control what component is shown.';


  public readonly svgClassList = ['fill-current', 'text-blue', 'transition-all', 'duration-200'];
  public readonly svgAttributes: SvgAttributes = { 'viewBox': '0 0 76 76' }

  @ViewChild(DynamicComponentDirective, {static: false}) private _dynamicComponent!: DynamicComponentDirective;
  @ViewChildren(SvgComponent) private _svgComponent!: QueryList<SvgComponent>

  private _selectedIndex = 0;

  public set selectedIndex(value) {
    this.setAnimations(value);
    this._selectedIndex = value;
    this.loadComponent()
  }

  public get selectedIndex() {
    return this._selectedIndex;
  }

  ngAfterViewInit(): void {
    this.loadComponent();
  }

  private setAnimations(i: number): void {
    if (this.selectedIndex !== i) {
      this._svgComponent.get(this.selectedIndex)?.swapClass('icon-28', 'icon-48')
      this._svgComponent.get(i)?.swapClass('icon-48', 'icon-28')
    }
  }
  
  
  private loadComponent(): void {
    const adItem = this.tabs[this.selectedIndex];
    
    const viewContainerRef = this._dynamicComponent?.viewContainerRef;
    viewContainerRef?.clear();
  
    viewContainerRef?.createComponent(adItem.component);
  }
}
