// eslint-disable-next-line @typescript-eslint/no-explicit-any
import { Directive, Input, EventEmitter, Output } from '@angular/core';
import { BaseInputs } from '../../common';

/**
 * Site Banner Base
 *
 * @export
 * @class SiteBannerBase
 * @typedef {SiteBannerBase}
 * @extends {BaseInputs}
 */
@Directive()
export class SiteBannerBase extends BaseInputs {
  /**
   * Hidden Details Text
   *
   * @type {string}
   * */
  @Input() hiddenDetails: string = '';

  /**
   * Identifies if details are shown or not
   *
   * @type {boolean}
   */
  @Input() showDetails: boolean = false;

  /**
   * Identifies if banner is closed on click
   *
   * @type {boolean}
   */
  @Input() close: boolean = false;

  /**
   * Banner Title
   *
   * @type {string}
   *
   */
  @Input() title: string = '';

  /**
   * Banner Main Text
   *
   * @type {string}
   */
  @Input() mainText: string = '';

  /**
   * Banner Details Text
   * Can be optional to use "Read More" or "Learn More" text
   *
   * @type {string}
   */
  @Input() details: string = 'Show Details';
  /**
   * Exiting the banner on click
   *
   * @type {*}
   *
   */
  @Output() bannerClosed: any = new EventEmitter();

  /**
   * Emits boolean as to whether the details are shown or not
   *
   * @type {EventEmitter<boolean>}
   */
  @Output() showDetailsChanged: EventEmitter<boolean> =
    new EventEmitter<boolean>();

  /**
   * When Show Details is clicked
   *
   * @public
   */
  public handleDetailsClicked(): void {
    this.showDetails = !this.showDetails;
    this.details = this.showDetails ? 'Hide Details' : 'Show Details';
    this.showDetailsChanged.emit(this.showDetails);
  }
  /**
   * @public
   */
  public handleCloseButton(): void {
    this.delete();
  }

  /**
   * @private
   */
  private delete(): void {
    this.close = !this.close;
    setTimeout(this.bannerClosed, 1000);
  }
}

