import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import { ToggleBase } from '../_helpers';

/**
 * Settings toggle
 *
 * @export
 * @class ToggleComponent
 * @typedef {ToggleComponent}
 * @extends {ToggleBase}
 */
@Component({
  selector: 'eds-toggle',
  templateUrl: './toggle.component.html',
  styleUrls: ['./toggle.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ToggleComponent extends ToggleBase {
  /**
   * Text to be displayed next to toggle
   *
   * @type {string}
   */
  @Input() text: string = 'toggle text';

  /**
   * Provides value to change checked state
   *
   * @type {boolean}
   */
  @Input() isChecked: boolean = false;

  /**
   * Provide image/icon src (optional)
   *
   * @type {string}
   */
  @Input() iconSrc: string = '';

  /**
   * Emits boolean as to whether the toggle is checked or not
   *
   * @type {EventEmitter<boolean>}
   */
  @Output() isCheckedChanged: EventEmitter<boolean> = new EventEmitter<boolean>();

  /**
   * When toggle is clicked
   *
   * @public
   */
  public handleToggleClicked(): void {
    this.isChecked = !this.isChecked;
    this.isCheckedChanged.emit(this.isChecked);
  }
}
