import { Directive, Input } from '@angular/core';
import { BaseInputs, HorizontalPlacement, VerticalPlacement } from '../../common';

/**
 * Base class for divider components
 *
 * @export
 * @class DividerBase
 * @typedef {DividerBase}
 */
@Directive()
export class DividerBase extends BaseInputs {
  /**
   * Text to be displayed within the divider
   *
   * @type {(string)}
   */
  @Input() text: string = '';
  /**
   * Builds tw classes based on textAlignment
   *
   * @public
   * @param {(HorizontalPlacement | VerticalPlacement)} textAlign
   * @returns {string}
   */
  public twClasses(textAlign: HorizontalPlacement | VerticalPlacement): string {
    // default - center
    let twClass =
      'before:h-2 before:w-2 after:h-2 after:w-2 before:flex-grow after:flex-grow';
    if (this.text) {
      switch (textAlign) {
        case 'top':
        case 'left':
          twClass = 'after:h-2 after:w-2 after:flex-grow';
          break;
        case 'bottom':
        case 'right':
          twClass = 'before:h-2 before:w-2 before:flex-grow';
          break;
        case 'center':
        default:
      }
    }

    return twClass;
  }
}
