import { Placement } from './placements';

export class TwClassHelpers {
  public static buildIconButtonClass(
    placement: Placement,
    base: string
  ): string {
    let extras = ' ';
    switch (placement) {
      case 'top':
        extras = 'flex-col py-32';
        break;
      case 'right':
        extras = 'flex-row';
        break;
      case 'bottom':
        extras = 'flex-col-reverse py-32';
        break;
      case 'left':
      default:
        extras = 'flex-row-reverse';
        break;
    }

    return `${base} ${extras}`;
  }

  public static buildIconClass(
    placement: Placement,
    size: string
  ): string {
    switch (placement) {
      case 'right':
        return `${size} mr-4`;
      case 'left':
        return `${size} ml-4`;
      case 'bottom':
        return `${size} mt-2`;
      case 'top':
      default:
        return `${size} mb-2`;
    }
  }
}
