import { Directive, EventEmitter, Input, Output } from '@angular/core';

import { ButtonTypes } from './button-types';
import { BaseInputs, Size } from '../../common';

@Directive()
export class ButtonBase extends BaseInputs {
  /**
   * Detemines what the button looks like (and type)
   *
   * @type {ButtonTypes}
   */
  @Input() type: ButtonTypes = 'primary';

  /**
   * Sizing options for button
   *
   * @type {Size}
   */
  @Input() size: Size = 'sm';

  /**
   * When true triggers progress spinner. ** not functioning **
   *
   * @type {boolean}
   */
  @Input() isLoading: boolean = false;

  /**
   * Whether showing on dark background
   *
   * @type {boolean}
   */
  @Input() isReversed: boolean = false;

  /**
   * Shows underline
   *
   * @type {boolean}
   */
  @Input() isUnderlined: boolean = false;

  /**
   * Displayed text on button
   *
   * @type {string}
   */
  @Input() text: string = '';

  /**
   * Button click output
   *
   * @type {EventEmitter<Event>}
   */
  @Output() buttonClick: EventEmitter<Event> = new EventEmitter<Event>();

  /**
   * Builds tailwind classes based on user inputs
   *
   * @readonly
   * @type {string}
   */
  get twClassesBasedOnInputs(): string {
    return `btn-${this.size} ${this.buildTwClasses(
      this.type,
      this.isReversed,
      this.isUnderlined,
      this.extraTwClasses
    )}`;
  }

  /**
   * Method to broadcast button click event
   */
  public handleButtonClick($event: Event): void {
    if (this.isDisabled) {
      return;
    }
    this.buttonClick.emit($event);
  }

  /**
   * @private
   * @param {ButtonTypes} type
   * @param {boolean} isReversed
   * @param {boolean} isUnderlined
   * @param {string} extras
   * @returns {string}
   */
  private buildTwClasses(
    type: ButtonTypes,
    isReversed: boolean,
    isUnderlined: boolean,
    extras: string
  ): string {
    let twClass = `btn-${type}${isReversed ? '-reversed ' : ' '}`;
    twClass += isUnderlined ? 'btn-underline ' : '';
    return `${twClass}${extras} `;
  }
}
