import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import { BaseInputs, Size } from '../common';

@Component({
  selector: 'eds-button-group',
  templateUrl: './button-group.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ButtonGroupComponent extends BaseInputs {
  @Input() isPrimaryFirst = false;
  @Input() primaryText = '';
  @Input() primaryExtraTwClasses = '';
  @Input() isPrimaryDisabled = false;
  @Input() secondaryText = '';
  @Input() secondaryExtraTwClasses = '';
  @Input() isSecondaryDisabled = false;
  @Input() tertiaryText = '';
  @Input() tertiaryExtraTwClasses = '';
  @Input() helperText = '';
  @Input() helperExtraTwClasses = '';
  @Input() size: Size = '2xs';

  @Output() primaryClick = new EventEmitter<Event>();
  @Output() secondaryClick = new EventEmitter<Event>();
  @Output() tertiaryClick = new EventEmitter<Event>();

  public handlePrimaryClick(event: Event): void {
    this.primaryClick.emit(event);
  }

  public handleSecondaryClick(event: Event): void {
    this.secondaryClick.emit(event);
  }

  public handleTertiaryClick(event: Event): void {
    this.tertiaryClick.emit(event);
  }
}
