import {
  ChangeDetectionStrategy,
  Component,
  Input,
} from '@angular/core';
import { BaseInputs, TextSize } from '../common';

/**
 * To display money
 *
 * @export
 * @class FeaturedMoneyComponent
 * @typedef {FeaturedMoneyComponent}
 * @extends {BaseInputs}
 */
@Component({
  selector: 'eds-featured-money',
  templateUrl: './featured-money.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FeaturedMoneyComponent extends BaseInputs {
  /**
   * Text color
   *
   * @type {string}
   */
  @Input() twTextColor: string = 'text-blue';

  /**
   * Text sizes
   *
   * @type {TextSize}
   */
  @Input() textSize: TextSize = '2xl';

  /**
   * Amount to display
   *
   * @type {string}
   */
  @Input() set amount(amount: string) {
    const textArray = amount.split('.');
    if (textArray.length > 1) {
      this._dollarAmount = textArray[0];
      const cents = textArray[1];
      this._centsAmount = cents.length > 1 ? cents : `${cents}0`;
    } else {
      const nMoney = amount;
      if (+nMoney > 0) {
        this._dollarAmount = nMoney;
        this._centsAmount = '00';
      }
    }
  }

  /**
   * Description placeholder
   *
   * @private
   * @type {string}
   */
  private _dollarAmount: string = '0';
  /**
   * Description placeholder
   *
   * @private
   * @type {string}
   */
  private _centsAmount: string = '00';

  /**
   * Description placeholder
   *
   * @readonly
   * @type {string}
   */
  get ariaLabel(): string {
    return `${this.dollarAmount} dollars and ${this.centsAmount} cents`;
  }

  /**
   * Description placeholder
   *
   * @readonly
   * @type {string}
   */
  get dollarAmount(): string {
    return this._dollarAmount;
  }

  /**
   * Description placeholder
   *
   * @readonly
   * @type {string}
   */
  get centsAmount(): string {
    return this._centsAmount;
  }
}
