import { Component, EventEmitter, Input, Output, ChangeDetectionStrategy } from '@angular/core';
import { AccountSummaryStanding } from './_helpers';
import { BaseInputs } from '../common/base-inputs';

@Component({
  selector: 'eds-account-summary',
  templateUrl: './account-summary.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AccountSummaryComponent extends BaseInputs{
  @Input() date: string = ''; 
  @Input() amount: string = ''; 
  @Input() set accountStanding(standing: AccountSummaryStanding) {
    this._accountColor = standing === 'good' ? 'bg-blue' : 'bg-red';
  } 
  
  @Output() PayNowClick = new EventEmitter<Event>();
  @Output() ViewBillClick = new EventEmitter<Event>();
  
  private _accountColor: string = 'bg-red';

  public getAccountColor(): string{
    return this._accountColor;
  }

  public handlePayNowClick(event: Event): void {
    this.PayNowClick.emit(event);
  }
  
  public handleViewBillClick(event: Event): void {
    this.ViewBillClick.emit(event);
  }
  
}
